@import url('https://fonts.googleapis.com/css2?family=Mulish&display=swap');
*{
    margin :0; 
    padding: 0; 
    box-sizing: border-box;
    font-family: "Muli",sans-serif;
   /*background: #d2dbdd; */
  
   }

.navbar {
    background-color: #fff !important;
}

.navbar-brand{
    font-size: 1.9rem !important;
    color: #565383 !important
}
.navbar a{
    font-size: 1.3rem;
    text-transform: capitalize;
}
.menu_active{
    font-weight: bold;
    border-bottom: 1px solid #565383;
}

.navbar a:hover{
    columns: #3498db;
}

@media(max-width: 991px)
{   
    .navbar-brand{
        font-size: 1.5rem !important;
    }
    .menu_active{
        font-weight: bold;
        border-bottom: none;
    }
}
/*
# Main landing Section 
-------------------------------------------------------------------------------*/
#header{
    width:100%;
    height: 80vh;
    margin: 50px;
}
#header h1{
    margin: 0 0 10px 0;
    font-size: 47px;
    font-weight: 700;
    color: black    ;
}
#header .brand-name{
    color:#3498db;
}
#header h2{
    color: #484848;
    font-size: 24px;
}

#header .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;

}

#header .btn-get-started:hover{
    background: #3498db;
    color: #fff;
}
.header-img{
    text-align: right;
    
}
#header .animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-dowm{
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}

@-keyframes up-dowm{
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}
footer p{
    padding: 0;
    margin: 0;
}


@media(max-width:991px)
{
    #header{
        height: 80vh;
        text-align:center;
    }
    #header .header-img{
        text-align:center;
    }
    #header .header-img img{
        width:60%;
    }
}

@media(max-width:768px)
{
    #header{
        margin-top: 20px;
    }
    #header h1{
        font-size: 28px;
    }
    #header h2{
        font-size: 18px;
        margin-bottom: 30px;
    }
    #header .header-img img{
        width: 70%;
    }
}










